// Slide.js - slider, hottem swiper
.swiper1 {
    padding: 70px 0 80px 0;

    .swiper {
        height: 800px;
    }

    .swiper-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .swiper-button-prev,
    .swiper-button-next {
        width: calc(var(--swiper-navigation-size)/3*27) !important;
        color: #fff !important;
        position: absolute;
        top: 50%;
    }

    .swiper-button-prev:after,
    .swiper-button-next:after {
        --swiper-navigation-size: 3rem !important;
    }

    .swiper-button-prev {
        left: 0 !important;
    }

    .swiper-button-next {
        right: 0 !important;
    }
}

.hottem {
    h2 {
        padding-bottom: 20px;
        font-size: 2.8rem;
        font-weight: bold;
        text-align: center;
        font-family: 'LINESeedKR-Bd';
    }

    .swiper2 {
        padding-bottom: 120px;

        .swiper-slide img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-top-right-radius: 50px;
            border-bottom-left-radius: 50px;
        }

        .swiper-button-prev,
        .swiper-button-next {
            // width:calc(var(--swiper-navigation-size)/3*27) !important;
            width: 0 !important;
            position: absolute;
            top: 50%;
            color: #888;
        }

        .swiper-button-prev {
            left: 24.3% !important;
        }

        .swiper-button-next {
            right: 24.3% !important;
        }

        .swiper-button-prev:after,
        .swiper-button-next:after {
            --swiper-navigation-size: 3rem !important;
        }

        .swiper-slide-prev {
            opacity: 0.3 !important;
        }

        .swiper-slide-next {
            opacity: 0.3 !important;
        }
    }
}

