//Home.js
.imgbox {
    width: 100%;
    margin: 0 auto;
    text-align: center;
    overflow: hidden;

    img {
        width: 100vw;
    }
}

.btnbox {
    margin: 10px auto 30px;
    text-align: center;

    .btn-link {
        padding: 3px 10px;
        // color: #b668ca;
        color:#f57c00; 
                
        text-decoration: none;
        margin-right: 10px;
        border: 2px solid #f57c00; 
        font-size: 1.6rem;
        text-align: center;
        border-radius: 30px;
        &:hover{
            background-color:#f57c00; 
            border:2px solid #f57c00;
            color: #fff;
        }
    }

    .btn:focus {
        box-shadow: none;
    }

    .btn-outline-primary {
        font-size: 1.6rem;
        border-radius: 30px;
    }
}

