//Detail.js
.detailpage {
    max-width: 1280px;
    padding-top: 120px;
    font-size: 1.6rem;

    .detailpage1 {
        padding-bottom: 5%;

        .col-md-6 {
            &:nth-of-type(1) {
                img {
                    padding-bottom: 3%;
                }
            }

            &:nth-of-type(2) {
                padding: 0 60px 0 50px;

                h2 {
                    font-size: 2.4rem;
                    font-weight: bold;
                }

                .table>:not(caption)>*>* {
                    box-shadow: none;
                }

                .table {
                    margin: 5% 0 1% 0;
                    font-family: 'SUIT-Regular';

                    tr {
                        height: 45px;

                        &:nth-of-type(1) {
                            td {
                                text-decoration: line-through;
                            }
                        }

                        &:nth-of-type(2) {
                            td {
                                font-weight: bold;
                                // color: #ff4800;
                                color:#f57c00; 
                            }
                        }
                    }

                    th {
                        width: 100px;
                        border-style: hidden;
                        padding: 5px 0;
                    }

                    td {
                        border-style: hidden;
                    }
                }
            }
        }

        .mb-3 {
            padding-bottom: 15px;

            .form-label {
                font-weight: bold;
            }

            .form-select {
                height: 45px;
            }

            .form-select:focus {
                box-shadow: none;
                border: 1px solid #ccc;
            }
        }

        hr {
            margin-bottom: 25px;
        }

        .detailbtnbox {
            margin: 0 auto;
            text-align: center;

            .btn {
                width: 48.5%;
                height: 50px;
                font-size: 1.6rem;
                color: #333;
                text-decoration: none;
                border-radius: 10px;

                &:nth-of-type(1) {
                    margin-right: 5px;
                    color: #fff;
                    background-color: #444;
                }

                &:nth-of-type(1):hover {
                    opacity: 0.8;
                }

                &:nth-of-type(2) {
                    background-color: #e9e9e9;
                    border: 2px solid #e9e9e9;
                }

                &:nth-of-type(2):hover {
                    opacity: 0.8;
                }
            }

            .btn:focus {
                box-shadow: none;
            }
        }

        .another {
            font-family: 'SUIT-Regular';
            padding-top: 30px;
            margin: 0 auto;

            img {
                width: 150px;
                padding-bottom: 10px;

                @media(max-width:1270px) {
                    width: 12vw;
                }

                @media(max-width:767px) {
                    width: 23vw;
                }
            }

            h4 {
                font-size: 1.3rem;
                font-weight: bold;
            }

            p {
                font-size: 14px;
            }
        }
    }
}

